body{
    background-color: #EAE7E2;
}
.emotion-buttons > button > img {
    width: 3rem;
    height: 3rem;
}
.emotion-buttons {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 0 auto;
    width: 100%;
}

.emotion-buttons > button {
    padding: 0.5rem;
    margin: 1rem;
    border-radius: 2rem;
    border: 2px solid #000;
    line-height: 0;
    color: #000;
    cursor: pointer;
    background-color: #585858;
}

.emotion-buttons > button.active-miserable {
    border-color: #7f58bc;
    background-color: #453F3F;
}

.emotion-buttons > button.active-sad {
    border-color: #5e69c3;
    background-color: #453F3F;
}

.emotion-buttons > button.active-neutral {
    border-color: #00c0cc;
    background-color: #453F3F;
}

.emotion-buttons > button.active-happy {
    border-color: #9acd65;
    background-color: #453F3F;
}

.emotion-buttons > button.active-awesome {
    border-color: #49b24b;
    background-color: #453F3F;
}